<template>
    <div ref="canvas" id="maincanvas" >
      <el-slider
      v-model="cameraOpt.fov" vertical
      show-input>
    </el-slider>

    </div>
</template>

<script>
import * as THREE from 'three'
import CameraControls from 'camera-controls'
CameraControls.install( { THREE: THREE } )
export default {
  name: 'maincanvas',
  props: {
      display:{
            type: Object,
            default: function() {
                return {
                    box:{ 
                      length:3000,
                      width:100,
                      height:100
                    },
                    color:'#FF0000',
                    length:100,
                    width:100,
                    height:100
                };
            }
      }
  },  
  data:()=>({
    _display:null,

    canvas:{
      width:null,
      height:null
    },
    scene:null, // 场景
    renderer:null,// 渲染器
    camera:null,// 透视相机
    cameraOpt:{
      fov:45,
      near:1,
      far:100000
    },
    light:null,   //光线
    clock:null,
    cameraControls:null,  //控制视角的

    vv:"",

    boxGroup:null,
    goodsGroups:[],

    raycaster:null,
    mouseVector:null,
    selectedObject:null,
    selectedObjectColorTemp:null
  }), 
  computed:{
    displayInfo:function(){
      if(!this.display){
        return {
          box:{ 
            length:8000,
            width:2200,
            height:2200
          },
        }
      }
      return this.display;
    }
  },
  watch: {
    display: {
      handler() { 
            this.generateBoxGroup();
            this.generateGoodsGroups();
      },
      deep: true
    },
    // "cameraOpt.fov":function(val){
    //     this.camera.fov=val;
    //     this.camera.updateProjectionMatrix();
    //     this.renderer.render(this.scene, this.camera);
    // }
  },
  created(){ 
    this.$nextTick(()=>{
      this.threeStart();
    });
  },
  mounted(){
    window.addEventListener('resize',this.onWindowResize);
    this.$refs.canvas.addEventListener( "mousemove", this.onDocumentMouseMove, false );
    this.$refs.canvas.addEventListener( "dblclick", this.onDblClick, false );
    this.$refs.canvas.addEventListener( "mousedown", this.onDocumentMouseDown, false );
  },
  methods:{
    initThree() {
        
      this.canvas.width = this.$refs.canvas.clientWidth;
      this.canvas.height = this.$refs.canvas.clientHeight;
      this.renderer = new THREE.WebGLRenderer({
          antialias : true
      });
      this.renderer.setSize(this.canvas.width, this.canvas.height); 
      this.$refs.canvas.appendChild(this.renderer.domElement);
      this.renderer.setClearColor(0xFFFFFF, 1.0);

          
    },
    initScene() {
      this.scene = new THREE.Scene();
      this.raycaster = new THREE.Raycaster();
      this.mouseVector = new THREE.Vector3();
    },
    initCamera() { 
      this.camera = new THREE.PerspectiveCamera(this.cameraOpt.fov, this.canvas.width / this.canvas.height, this.cameraOpt.near, this.cameraOpt.far);
      //camera = new THREE.OrthographicCamera( window.innerWidth / - 2, window.innerWidth / 2, window.innerHeight / 2, window.innerHeight / - 2, 10, 1000 );
      
      
      
      this.camera.position.x = 3300;
      this.camera.position.y = 3000;
      this.camera.position.z = 10000; 
      this.camera.lookAt({
          x : 0,
          y : 50,
          z : 0
      });
      //按住鼠标转
      // new OrbitControls( this.camera, this.renderer.domElement );
      // const cameraControls = 
    
    }, 
    initClockAndCameraControls(){
      this.clock = new THREE.Clock();
      this.cameraControls=new CameraControls(this.camera, this.renderer.domElement );
    },
    initLight() {  
 
      let ambientLight = new THREE.AmbientLight( 0x000000,1);
      this.scene.add( ambientLight );

      let lights = [];
      lights[ 0 ] = new THREE.PointLight( 0xeeeeee, 1, 0 ,6);
      lights[ 1 ] = new THREE.PointLight( 0xeeeeee, 1, 0 ,6);
      lights[ 2 ] = new THREE.PointLight( 0xeeeeee, 1, 0 ,6);

      lights[ 0 ].position.set( 3000, 5000, 3000 );
      lights[ 1 ].position.set( 7000, 5000, 10000 );
      lights[ 2 ].position.set( - 5000, - 5000, - 3000 );


      this.scene.add( lights[ 0 ] );
      this.scene.add( lights[ 1 ] );
      this.scene.add( lights[ 2 ] );
    },
    initObject(){
      // this.obj1();
      
      // this.obj2();

      // this.obj3();


      this.generateBoxGroup();
      // this.center();

      // this.horizontal();
      
      
    },
    animation()
    { 
        let delta = this.clock.getDelta();
        this.cameraControls.update( delta );
        this.renderer.render(this.scene, this.camera); 
        requestAnimationFrame(this.animation);
        // this.vv=" x:"+parseInt(this.camera.position.x) +"   y:"+ parseInt(this.camera.position.y) +"    z:"+ parseInt(this.camera.position.z) ;
        // this.vv+=" ______ x:"+parseInt(this.camera.rotation.x) +"   y:"+ parseInt(this.camera.rotation.y) +"    z:"+ parseInt(this.camera.rotation.z) ;
        // this.vv+=" fov:"+ this.cameraOpt.fov;
    },
    threeStart() { 
      this.initThree();
      this.initCamera();
      this.initClockAndCameraControls(); 
      this.initScene();
      this.initLight();
      this.initObject(); 
        
      this.animation();

    },
    center(){
      var geometry = new THREE.BoxGeometry( 1, 1, 1 );
      var material = new THREE.MeshBasicMaterial( {color: 0x888888} );
      var cube = new THREE.Mesh( geometry, material ); 
      this.scene.add( cube );
    }
    ,
    horizontal(){
      var size = 6000;
      var divisions = 50;
      var gridHelper = new THREE.GridHelper(size, divisions, 0x1231c3, 0x135133);
      this.scene.add(gridHelper); 
    },
    getGoodInitialPostion(){
      return  {
        x:-this.displayInfo.box.width/2+5,
        y:-this.displayInfo.box.height/2+5,
        z:-this.displayInfo.box.length/2+5
      };
    },
    generateBoxGroup(){ 
        if(this.boxGroup!=null){
            this.scene.remove(this.boxGroup);
            this.boxGroup=null;
        }
        this.boxGroup = new THREE.Group();
        let material =new THREE.MeshPhongMaterial({
          color: 0xBE5B65, 
          side: THREE.DoubleSide,
          flatShading: THREE.FlatShading
        })

        //左边
        
        let groupLeft = new THREE.Group();
        let geometryLeft = new THREE.CubeGeometry(1,this.displayInfo.box.height,this.displayInfo.box.length);
        let cubeLeft = new THREE.Mesh(geometryLeft, material);   
        var lineLeft = new THREE.LineSegments( geometryLeft, new THREE.LineBasicMaterial( { color: 0x000000 } ) );
        
        groupLeft.add(cubeLeft,lineLeft);
        groupLeft.position.x=-this.displayInfo.box.width/2;
        groupLeft.position.y=0;
        groupLeft.position.z=0; 

        let groupBottom = new THREE.Group();
        let geometryBottom = new THREE.CubeGeometry(this.displayInfo.box.width,1,this.displayInfo.box.length);
        let cubeBottom = new THREE.Mesh(geometryBottom, material);   
        var lineBottom = new THREE.LineSegments( geometryBottom, new THREE.LineBasicMaterial( { color: 0x000000 } ) );
 
        groupBottom.add(cubeBottom,lineBottom);
        groupBottom.position.x=0;
        groupBottom.position.y=-this.displayInfo.box.height/2;
        groupBottom.position.z=0; 


        let groupRight = new THREE.Group();
        let geometryRight = new THREE.CubeGeometry(this.displayInfo.box.width,this.displayInfo.box.height,1);
        let cubeRight = new THREE.Mesh(geometryRight, material);   
        var lineRight = new THREE.LineSegments( geometryRight, new THREE.LineBasicMaterial( { color: 0x000000 } ) );
 

        groupRight.add(cubeRight,lineRight);
        groupRight.position.x=0;
        groupRight.position.y=0;
        groupRight.position.z=-this.displayInfo.box.length/2; 

        this.boxGroup.add(groupLeft);
        this.boxGroup.add(groupBottom);

        this.boxGroup.add(groupRight); 


 

        
        this.scene.add( this.boxGroup );
    },
    generateGoodsGroups(){

        if(this.goodsGroups!=null&&this.goodsGroups.length>0){
            this.goodsGroups.forEach(goodsGroup => {
              this.scene.remove(goodsGroup);
            });
            this.goodsGroups=[]; 
        }
        // let _initPos=this.getGoodInitialPostion();
        let initPos=this.getGoodInitialPostion();
        this.displayInfo.goodsManager.realGoods.forEach(good => {
          if(!good.pos){
            //没有设置位置
            return;
          }


          let group = new THREE.Group();

          let geometry = new THREE.CubeGeometry(good.width,good.height,good.length);
          let material =new THREE.MeshPhongMaterial( {
            color: good.color,
            side: THREE.DoubleSide,
            flatShading: THREE.FlatShading
          } ) 
          let cube = new THREE.Mesh(geometry, material);  
          var line = new THREE.Line( geometry, new THREE.LineDashedMaterial( { color: 0x000000 } ) );

          group.add(cube,line);
          
   
          //初始坐标 + 元素中心点位移 + 加计算出来的相对的位置

          group.position.x=initPos.x+(good.width/2)+good.pos.x;
          group.position.y=initPos.y+(good.height/2)+good.pos.y;
          group.position.z=initPos.z+(good.length/2)+good.pos.z;

         


          group.userData=good;

          this.goodsGroups.push(group);
          this.scene.add(group);
          
          

        });


        
        this.otherboxShow();

        
         
//         var line = new THREE.Line( geometry, new THREE.LineDashedMaterial( { color: 0x000000 } ) );

//         this.group.add(cube,line);
//         this.group.position.x=0;
//         this.group.position.y=0;
//         this.group.position.z=0; 
//         this.scene.add( this.group );


    },
    otherboxShow(){

        let initPos=this.getGoodInitialPostion();
        this.displayInfo.otherbox.forEach(otherbox => {
          if(!otherbox.pos||otherbox.isFalse){
            //没有设置位置
            return;
          }


          let group = new THREE.Group();

          let geometry = new THREE.CubeGeometry(otherbox.width,otherbox.height,otherbox.length);
          let material =new THREE.MeshPhongMaterial( {
            color: "#ffffff",
            side: THREE.DoubleSide,
            flatShading: THREE.FlatShading
          } ) 
          if(otherbox.isBWOK){
            material.color.set("#000000")
          }
          let cube = new THREE.Mesh(geometry, material);  
          var line = new THREE.Line( geometry, new THREE.LineDashedMaterial( { color: 0x000000 } ) );

          group.add(cube,line);
          
   
          //初始坐标 + 元素中心点位移 + 加计算出来的相对的位置

          group.position.x=initPos.x+(otherbox.width/2)+otherbox.pos.x;
          group.position.y=initPos.y+(otherbox.height/2)+otherbox.pos.y;
          group.position.z=initPos.z+(otherbox.length/2)+otherbox.pos.z;
 
 
          group.userData=otherbox;
          this.goodsGroups.push(group);
          
          this.scene.add(group); 

        });
    },
    onWindowResize(){
      if(!this.$refs.canvas)return ;
        this.canvas.width = this.$refs.canvas.clientWidth;
        this.canvas.height = this.$refs.canvas.clientHeight;
              // 重新设置相机宽高比例
        this.camera.aspect = this.canvas.width / this.canvas.height;
              // 更新相机投影矩阵
        this.camera.updateProjectionMatrix();
              // 重新设置渲染器渲染范围
        this.renderer.setSize(this.canvas.width, this.canvas.height); 
    },


    onDocumentMouseMove( event ) {
        event.preventDefault();
        if ( this.selectedObject ) {  
            this.selectedObject.material.color.set( this.selectedObjectColorTemp);
            this.selectedObject = null;
        }
        var intersects = this.getIntersects( event.layerX, event.layerY );
        if ( intersects.length > 0 && this.selectedObject!=intersects[0].object) {
            var res = intersects.filter( function ( res ) {
                return res && res.object;
            } )[ 0 ];
            if ( res && res.object ) {
                this.selectedObject = res.object;   
                this.selectedObjectColorTemp="#"+this.selectedObject.material.color.getHexString();
                this.selectedObject.material.color.set( '#69f' ); 
            }
        }
    },

    onDblClick( event ) {
      event.preventDefault(); 

      var intersects = this.getIntersects( event.layerX, event.layerY );
      
      if ( intersects.length > 0) {
          var res = intersects.filter( function ( res ) {
              return res && res.object ;
          } )[ 0 ]; 
          if ( res && res.object ) {
            console.log(res.object.parent.parent.userData)
            console.log(res.object.parent.userData)
            this.scene.remove(res.object.parent.parent);
            this.scene.remove(res.object.parent);
          }
      }
    },

    onDocumentMouseDown( event ) {
      event.preventDefault(); 

      var intersects = this.getIntersects( event.layerX, event.layerY );
      
      if ( intersects.length > 0) {
          var res = intersects.filter( function ( res ) {
              return res && res.object ;
          } )[ 0 ]; 
          if ( res && res.object ) {
            console.log(res.object.parent.userData) 
          }
      }
    },
    getIntersects( x, y ) {
        x = ( x / window.innerWidth ) * 2 - 1;
        y = - ( y / window.innerHeight ) * 2 + 1;
        this.mouseVector.set( x, y, 0.5 );
        this.raycaster.setFromCamera( this.mouseVector, this.camera );
        return this.raycaster.intersectObject( this.scene, true );
    }
  }
}
</script>

