<template>
  <div id="app">
    <!-- <img alt="Vue logo" src="./assets/logo.png"> -->
    <!-- <HelloWorld msg="Welcome to Your Vue.js App"/> -->
    <i class="el-icon-menu" id="c"   @click="drawer=!drawer"></i>
    <a class="link" target="blank" href="https://wenku.baidu.com/view/d4b289a7b0717fd5360cdc56.html">https://wenku.baidu.com/view/d4b289a7b0717fd5360cdc56.html</a>
    

    <maincanvas :display="display"></maincanvas>
    
    <el-drawer
      title="数据"
      :visible.sync="drawer" size="600px"
       direction="rtl"
      >
        
        <div style="text-align: right;padding: 0px 20px 10px 0px;">
          <el-button type="primary" @click="doitClick" :disabled="!hasSelect">do it</el-button>
        </div>
      <el-collapse >
        <el-collapse-item  name="1">
          
          <template slot="title"><div class="collapse_title">货物
            <i @click.prevent.stop="showAddGoodsDialog" class="el-icon-circle-plus-outline"></i>
            </div></template>


            <el-table size="mini" ref="goodstable"  border :data="this.opt.goods" style="width: 100%" @row-dblclick="goodsrowdbclick" type=index  @selection-change="handleSelectionChange_goods">
              <el-table-column type="selection" width="40">
              </el-table-column>
              <el-table-column prop="name" label="名称" >
                <template  slot-scope="scope"> 
                   {{scope.row.name}}
                </template>
              </el-table-column>
              <el-table-column prop="color" label="颜色" width="60px" align="center">
                <template  slot-scope="scope"> 
                   <div class="colorcell" :style="{'background-color':scope.row.color}">&nbsp;</div>
                </template>
              </el-table-column>

              <el-table-column   label="长宽高" >
                <template  slot-scope="scope"> 
                   {{scope.row.length}}*{{scope.row.width}}*{{scope.row.height}}
                </template>
              </el-table-column>
              <el-table-column prop="num" label="数量" > 
              </el-table-column> 
              <el-table-column prop="inout" label="先进先出" > 
              </el-table-column> 
              <el-table-column   label="操作" width="60px" align="center" >
                <template  slot-scope="scope">  
                    <i  class="el-icon-delete" @click="goodsdel(scope.$index)"></i>  
                </template>
              </el-table-column>

            </el-table> 

 
        </el-collapse-item>
        <el-collapse-item   name="2">
          
          <template slot="title"><div class="collapse_title">集装箱
            <i @click.prevent.stop="showAddboxesDialog" class="el-icon-circle-plus-outline"></i>
            </div>
          </template>
          

          <el-table size="mini" ref="boxestable"  border :data="this.opt.boxes" style="width: 100%" @row-dblclick="boxesrowdbclick" type=index @selection-change="handleSelectionChange_boxes" >
              <el-table-column type="selection" width="40">
              </el-table-column>
              <el-table-column prop="name" label="名称" >
                <template  slot-scope="scope"> 
                   {{scope.row.name}}
                </template>
              </el-table-column> 

              <el-table-column   label="长宽高" >
                <template  slot-scope="scope"> 
                   {{scope.row.length}}*{{scope.row.width}}*{{scope.row.height}}
                </template>
              </el-table-column>
              <el-table-column   label="操作" width="60px" align="center" >
                <template  slot-scope="scope">  
                    <i  class="el-icon-delete" @click="boxesdel(scope.$index)"></i>  
                </template>
              </el-table-column>

            </el-table> 



        </el-collapse-item>
      </el-collapse>
    </el-drawer>



















    <el-dialog   :close-on-click-modal="false" 
      title="添加货物"
      :visible.sync="dialog.goodsshow"
      width="700px"> 
      <div class="diagloggood">
        <goodcanvas :good="dialog.goodform" ></goodcanvas>
      </div>
      <el-form :inline="true" size="mini" label-position="left"   ref="goodform" :model="dialog.goodform"  :rules="dialog.goodformrules">

        <div>
          <el-form-item label="名称" prop="name" >
            <el-input style="width:150px"  v-model="dialog.goodform.name"></el-input>
          </el-form-item>  

          <el-form-item label="颜色" style="padding-left:20px"   >
            <colorPicker v-model="dialog.goodform.color" />
          </el-form-item>
        </div>
        <!-- <el-form-item label="净重">
          <el-input style="text-align:center;width:80px" v-model.number="dialog.goodform.netweight"></el-input>
        </el-form-item> -->
        <!-- <el-form-item label="毛重"> -->
        <el-form-item label="重量">
          <el-input style="text-align:center;width:80px" v-model.number="dialog.goodform.grossweight"></el-input>
        </el-form-item>
        <!-- <el-form-item label="单位">
          <el-select style="width:100px" size="mini"  v-model="dialog.goodform.unit"   placeholder="请选择"> 
            <el-option v-for="item in units" :key="item" :label="item" :value="item"></el-option>
          </el-select>
        </el-form-item> -->
        <el-form-item label="数量"  prop="num">
          <el-input-number style="width:100px" v-model="dialog.goodform.num"  :step="20" :min="1"  ></el-input-number> 
        </el-form-item> 
        <!-- <el-form-item label="堆码级别(1最低)">
          <el-input style="text-align:center;width:70px" v-model.number="dialog.goodform.stackinglevel"></el-input>
        </el-form-item> 
         -->
        <el-form-item label="描述" >
          <el-input  style="width:350px"  v-model="dialog.goodform.desc" autosize  ></el-input>
        </el-form-item>  
        <el-form-item label="先进先出级别(1为最先出放外面)" >
          <el-input-number style="width:100px" v-model="dialog.goodform.inout"  :step="1" :min="0"  ></el-input-number> 
        </el-form-item>  
        <el-tabs v-model="dialog.tabval" >
          <el-tab-pane label="尺寸" name="1">
            <div>
              <el-form-item label="长"  prop="length">
                
                <el-input-number style="width:100px" v-model="dialog.goodform.length"  :step="20" :min="1"  ></el-input-number> 
              </el-form-item> 
            </div>
            <div>
              <el-form-item label="宽" prop="width">
                <el-input-number style="width:100px" v-model="dialog.goodform.width"  :step="20" :min="1" ></el-input-number>  
              </el-form-item> 
            </div>
            <div>
              <el-form-item label="高" prop="height">
                <el-input-number style="width:100px" v-model="dialog.goodform.height"  :step="20" :min="1" ></el-input-number>   
              </el-form-item> 
            </div> 
          </el-tab-pane>
          
          <el-tab-pane label="摆放限制" name="2">
             
            <el-table size="mini" highlight-current-row @current-change="handleCurrentChange"
              :data="dialog.goodform.restrictions"
              style="width: 100%">
              <el-table-column label="摆放方向" width="130" align="center">
                <template  slot-scope="scope">
                   <el-checkbox  size="mini" v-model="scope.row.isselect">{{scope.row.direction}}</el-checkbox>
                </template>
              </el-table-column>

              <el-table-column label="是否承重面" width="100" align="center">
                <template  slot-scope="scope">
                   <el-checkbox  size="mini" v-model="scope.row.isbear"></el-checkbox>
                </template>
              </el-table-column>

              <el-table-column label="承托级别" width="100">
                <template  slot-scope="scope">
                  <el-input  size="mini" style="width:80px" v-model.number="scope.row.bearlevel"></el-input> 
                </template>
              </el-table-column>

              <el-table-column label="自身堆码限制" width="100" align="center">
                <template  slot-scope="scope"> 
                   <el-checkbox  size="mini" v-model="scope.row.isstack"></el-checkbox>
                </template>
              </el-table-column>

              <el-table-column label="自身最大堆码层数" >
                <template  slot-scope="scope">
                  <el-input  size="mini" style="width:80px" v-model.number="scope.row.stacklevel"></el-input> 
                </template>
              </el-table-column>


            </el-table> 
 
 
            
          </el-tab-pane> 
        </el-tabs>


      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="dialog.goodsshow = false">取 消</el-button>
        <el-button type="primary" @click="saveGood">确 定</el-button>
      </span>
    </el-dialog>








    <el-dialog   :close-on-click-modal="false" 
      title="添加集装箱"
      :visible.sync="dialog.boxesshow"
      width="700px">  
      <el-form :inline="true" size="mini" label-position="left"   ref="boxform" :model="dialog.boxform"  :rules="dialog.boxformrules">

        <div>
          <el-form-item label="名称" prop="name" >
            <el-input style="width:150px"  v-model="dialog.boxform.name"></el-input>
          </el-form-item>  
 
        </div> 
        
        <div>
          <el-form-item label="描述" >
            <el-input  style="width:350px"  v-model="dialog.boxform.desc" autosize  ></el-input>
          </el-form-item>   
        </div> 
        <div>
              <el-form-item label="长"  prop="length">
                
                <el-input-number style="width:200px" v-model="dialog.boxform.length"  :step="200" :min="1"  ></el-input-number> 
              </el-form-item> 
     
        </div> 
        <div>
              <el-form-item label="宽" prop="width">
                <el-input-number style="width:200px" v-model="dialog.boxform.width"  :step="200" :min="1" ></el-input-number>  
              </el-form-item> 
        </div> 
        <div>
              <el-form-item label="高" prop="height">
                <el-input-number style="width:200px" v-model="dialog.boxform.height"  :step="200" :min="1" ></el-input-number>   
              </el-form-item> 
       
        </div>  
 
             
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="dialog.boxesshow = false">取 消</el-button>
        <el-button type="primary" @click="saveBox">确 定</el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>


import maincanvas from './components/maincanvas.vue'

import goodcanvas from './components/goodcanvas.vue'
import {doit,deepClone} from './util/index.js'

export default {
  name: 'App',
  components: {
    maincanvas,
    goodcanvas
  }, 
  data:()=>({
    
    drawer:true,//右侧抽屉

    opt:{
      goods:[

      ], 
      boxes:[

      ],
      goodsSel:[

      ],
      boxesSel:[

      ]
    },
    units:[
      "克",
      "千克",
      "吨",
      "磅"
    ],
    hasSelect:false,
    restrictions:[
      {
        flag:'x-',
        isselect:true,
        direction:'立放,正向',
        isbear:true,
        bearlevel:1,
        isstack:true,
        stacklevel:1 
      },
      {
        flag:'x|',
        isselect:true,
        direction:'立放,横向',
        isbear:true,
        bearlevel:1,
        isstack:true,
        stacklevel:1 
      },
      
      {
        flag:'y-',
        isselect:true,
        direction:'侧放,正向',
        isbear:true,
        bearlevel:1,
        isstack:true,
        stacklevel:1 
      },
      {
        flag:'y|',
        isselect:true,
        direction:'侧放,横向',
        isbear:true,
        bearlevel:1,
        isstack:true,
        stacklevel:1 
      },
      
      {
        flag:'z-',
        isselect:true,
        direction:'卧放,正向',
        isbear:true,
        bearlevel:1,
        isstack:true,
        stacklevel:1 
      },
      {
        flag:'z|',
        isselect:true,
        direction:'卧放,横向',
        isbear:true,
        bearlevel:1,
        isstack:true,
        stacklevel:1 
      }
    ],
    dialog:{
      goodsshow:false,
      goodform:{
        id:null,
        name:"",
        desc:"",
        netweight:0,
        grossweight:0,
        unit:"",
        stackinglevel:1,
        color:'',
        length:0,
        width:0, 
        height:0,
        num:0,
        restrictions:[],
        restrictionflag:'',
        inout:0,
      }, 
      goodformrules:{
        name :[{required: true, message: '请输入', trigger: 'blur'}],
        num :[{required: true, message: '请输入', trigger: 'blur'}],
        length :[{required: true, message: '请输入', trigger: 'blur'}],
        width :[{required: true, message: '请输入', trigger: 'blur'}],
        height :[{required: true, message: '请输入', trigger: 'blur'}],
         
      },
      tabval:"1",
      boxesshow:false,
      boxform:{
        id:null,
        name:"",
        desc:"",
        length:0,
        width:0, 
        height:0,
      },
      boxformrules:{
        name :[{required: true, message: '请输入', trigger: 'blur'}],
        length :[{required: true, message: '请输入', trigger: 'blur'}],
        width :[{required: true, message: '请输入', trigger: 'blur'}],
        height :[{required: true, message: '请输入', trigger: 'blur'}],
      },
    }
    ,

    display:null
  }),
  created(){
    this.$nextTick(()=>{
      this.initOpt();
      this.dialog.goodform.restrictions=this.restrictions;
    });
  },
  
  methods:{  
    initOpt(){
      let opt=localStorage.getItem('opt');
      if(opt!=null){
        opt=JSON.parse(opt);
        this.opt=opt;
      }
    },
    saveOpt(){
      let _opt = deepClone(this.opt);
      delete _opt.goodsSel;
      delete _opt.boxesSel;
      localStorage.setItem("opt", JSON.stringify(_opt));
    },

    goodsrowdbclick(o){ 
      this.dialog.goodform=deepClone(o);
      
      this.dialog.tabval="1"

      this.dialog.goodsshow=true;
    },
    boxesrowdbclick(o){ 
      this.dialog.boxform=deepClone(o);
       
      this.dialog.boxesshow=true;
    },
    showAddGoodsDialog(){
      this.dialog.goodform={
        id:null,
        name:"",
        desc:"",
        netweight:0,
        grossweight:0,
        unit:"",
        stackinglevel:1,
        color: "#"+Math.random().toString(16).slice(-6),
        length:200,
        width:400, 
        height:600,
        num:100,
        restrictions:this.restrictions,
        restrictionflag:'',
        inout:0
      }
      this.dialog.tabval="1"

      this.dialog.goodsshow=true;
      // this.$refs.goodcanvas.addGeometry(this.dialog.goodform.color,this.dialog.goodform.length,this.dialog.goodform.width,this.dialog.goodform.height)
    },


    showAddboxesDialog(){
      this.dialog.boxform={
        id:null,
        name:"",
        desc:"",
        length:8000,
        width:2000, 
        height:2000,
      }; 
      this.dialog.boxesshow=true;
    },
    handleCurrentChange(val){
      if(val){
        this.dialog.goodform.restrictionflag=val.flag;
      }
    },
    saveGood(){ 

        if(this.dialog.goodform.restrictions.filter((o)=>{return o.isselect}).length==0){
          
          this.$message({
              showClose: true,
              message: '至少选择一条摆放方式',
              type: 'error'
            });


          return;
        }
      
        this.$refs.goodform.validate((valid) => {
          if (!valid) { 
            return;
          } 
          if(this.dialog.goodform.id==null)
          {
            this.dialog.goodform.id=new Date().getTime();

            this.opt.goods.push(this.dialog.goodform);
            
          }else{
            let isSel=false;
            if(this.opt.goodsSel){
              for (let index = 0; index < this.opt.goodsSel.length; index++) {
                let good = this.opt.goodsSel[index];
                if(good.id==this.dialog.goodform.id){  
                  isSel=true;
                }
              }
            }

            for (let index = 0; index < this.opt.goods.length; index++) {
              let good = this.opt.goods[index];
              if(good.id==this.dialog.goodform.id){  
                this.opt.goods.splice(index,1,this.dialog.goodform); 
              }
            } 

            if(isSel){
              this.$refs.goodstable.toggleRowSelection(this.dialog.goodform);
            }

          }  
          this.saveOpt();
          this.dialog.goodsshow = false;

        }); 

    },
    saveBox(){
      this.$refs.boxform.validate((valid) => {
          if (!valid) { 
            return;
          } 
          if(this.dialog.boxform.id==null)
          {
            this.dialog.boxform.id=new Date().getTime();

            this.opt.boxes.push(this.dialog.boxform);
            
          }else{
            
            let isSel=false; 
            if(this.opt.boxesSel){
              for (let index = 0; index < this.opt.boxesSel.length; index++) {
                let box = this.opt.boxesSel[index];
                if(box.id==this.dialog.boxform.id){  
                  isSel=true;
                }
              }
            }
            for (let index = 0; index < this.opt.boxes.length; index++) {
              let box = this.opt.boxes[index];
              if(box.id==this.dialog.boxform.id){ 
                this.opt.boxes.splice(index,1,this.dialog.boxform); 
              } 
            } 
            if(isSel){
              this.$refs.boxestable.toggleRowSelection(this.dialog.boxform);
            }


          }  
          this.saveOpt();
          this.dialog.boxesshow = false;

        }); 
    },


    boxesdel(index){
      let delbox=this.opt.boxes[index];
      this.opt.boxes.splice(index, 1);





      let h = this.$createElement;
      let msg= this.$message({
        message: h('p', null, [
          h('div',null, "["+delbox.name+"]已删除"),
          h('el-button', { 
            attrs:{
              "type":"text"
            },
            class: 'undo',
            on:{
            click: ()=>{
               
              this.opt.boxes.splice(index, 0,delbox);
              this.saveOpt();
              msg.close();
            }
          } }, '撤销')
        ]),
        
        duration:5000,
        showClose:true
      });
 
       
       
      this.saveOpt();
    },

    goodsdel(index){
      let delgood=this.opt.goods[index];
      this.opt.goods.splice(index, 1);





      let h = this.$createElement;
      let msg= this.$message({
        message: h('p', null, [
          h('div',null, "["+delgood.name+"]已删除"),
          h('el-button', { 
            attrs:{
              "type":"text"
            },
            class: 'undo',
            on:{
            click: ()=>{
               
              this.opt.goods.splice(index, 0,delgood);
              this.saveOpt();
              msg.close();
            }
          } }, '撤销')
        ]),
        
        duration:5000,
        showClose:true
      });
 
       
       
      this.saveOpt();
    },

    handleSelectionChange_boxes(val){
      this.opt.boxesSel=val;
      this.hasSelectHandle(); 
    },
    handleSelectionChange_goods(val){
      this.opt.goodsSel=val;
      this.hasSelectHandle();
      
    },
    hasSelectHandle(){

      if(this.opt.boxesSel && this.opt.goodsSel && this.opt.boxesSel.length>0&&this.opt.goodsSel.length>0){
        this.hasSelect=true; 
      }else{
        this.hasSelect=false; 
      }
    },
 
    doitClick(){
      let putBoxResult=doit(this.opt.goodsSel,this.opt.boxesSel);
      this.display=putBoxResult;
    }
  }
}
</script>

<style>
  *{ 
  margin: 0;
  padding: 0;
  }
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; 
  color: #2c3e50; 
  width: 100%; height: 100% 
}

  html,body, canvas { 
    width: 100%; 
    height: 100% 
  }
  .link{
    position:absolute;
    color: #2c3e50;
    right: 0px;
    bottom: 0px;
  }

  div#maincanvas,div#goodcanvas {
      border: none; 
      width: 100%;
      height: 100%;
      background-color: #EEEEEE;
      overflow: hidden;
  }
  #c{
    position:absolute;
    right: 0px;
    cursor: pointer;
    font-size: 32px;
  }
  .collapse_title{
    padding-left: 20px;
    position: relative;
    display: block;
    width:100%;
  }
  .collapse_title i{
    position:absolute;
    right: 5px;
    top:calc(50% - 10px);
    cursor:default;
    font-size: 20px;
  }
  .collapse_title i:hover{
    background-color:rgba(200, 200,200, 0.6) ;
  }
  .m-colorPicker{
    z-index: 99999;
    margin-top: 7px;
  }
  .diagloggood{    
    position: absolute;
    right: 33px;
    top: 30px;
    width: 200px;
    height: 200px;
  }
  .el-collapse-item__content{
    padding: 20px 20px 0;
  }
  .colorcell{
    width: 20px;
    height:  20px;
    margin: auto;
  }
  .cell i{
    cursor: pointer;
  }

  .el-message .undo{

    position: absolute;
    right: 50px;
    cursor: pointer;
    top: 50%;
    margin: 0px;
    transform: translateY(-50%);
  }
  #maincanvas .el-slider{
    position:absolute;
    left: 0px;
    height: 200px;
  }
</style>
