
//数组笛卡尔积 参数是 包含多个数组的数组
function cartesianProductOf(array) {
    return Array.prototype.reduce.call(array,    
        function(a, b) { 
            var ret = [];
            a.forEach(function(a) {
                b.forEach(function(b) {
                    ret.push(a.concat([b]));
                });
            });
        return ret;
    }, [[]]);
  }


  //数组 顺序全排列
function permute(arr){
    var rst=[],cur=[];
    function fn(arr){
        var ch;
        for(var i=0;i<arr.length;i++){
            ch=arr.splice(i,1)[0]
            cur.push(ch)
            if(arr.length==0){
                rst.push(cur.slice())
            }
            fn(arr)
            arr.splice(i,0,ch)
            cur.pop()
		}
		return rst
	}
	return fn(arr)
}


//该方法将 目前所有的 货物的所有摆放形式 
// 列出来 多种货物支持的摆放方式的笛卡尔积   
function GoodsRestrictionsManager(goods){
 
    this.allGoods=[];
    this.resGoods=[];

    //产品遍历
    goods.forEach(good => { 

        let thegoodarray=[];

        //产品支持的 摆放方式遍历
        good.restrictions.forEach(res => {
            if(!res.isselect){
                return;
            }
            // console.log("good:"+good.name+"  ||  res:"+res.direction)

            let gl=good.length;
            let gw=good.width;
            let gh=good.height; 
            //对于不同的摆放方式 切换一下长宽高 此处旋转不好用 后面需要加减
            switch (res.flag) {
                case "x-":   
                    break;
                case "x|":  
                    gl=good.width;
                    gw=good.length;
                    break;
                case "y-": 
                    gw=good.height;
                    gh=good.width;
                    break;
                case "y|": 
                    gh=good.width;
                    gw=good.length;
                    gl=good.height;
                    break;
                case "z-": 
                    gh=good.length;
                    gl=good.height;
                    break;
                case "z|": 
                    gh=good.length;
                    gw=good.height; 
                    gl=good.width; 
                    break; 
            }

            let newGood={
                height:gh,
                length:gl,
                width:gw,
                flag:res.flag,
                name:good.name,
                id:good.id,
                color:good.color,
                direction:res.direction,
                num:good.num,
                inout:(good.inout?good.inout:0)
            } 
            thegoodarray.push(newGood);
        }); 
        
        this.allGoods.push(thegoodarray);
    });
 
    //笛卡尔积
    this.resGoods=cartesianProductOf(this.allGoods); 



    //按照进出 排序
    let newResGoods=[];
    console.log(this.resGoods);
    this.resGoods.forEach(goods=>{
        console.log(goods);
        let pGoods=permute(goods);

        pGoods.forEach(pGoodArray=>{
            let isOk=true;
            let inOutTemp=0;
            for (let index = 0; index < pGoodArray.length; index++) {
                const g = pGoodArray[index];
                if(inOutTemp<=0 || !g.inout || inOutTemp>=g.inout  || g.inout==0 ){
                    inOutTemp=g.inout;
                }else{
                    isOk=false; 
                }
            }
            if(isOk){
                newResGoods.push(pGoodArray);
            }
        });
        console.log(newResGoods);
        console.log("VV__")
    }) 

    this.resGoods=newResGoods;
}



//一批货物 用于取下一个元素
//方便：取下一个同类型 反馈当前已成功装箱 内部删除 
//      取其他类型进行尝试
function GoodsManager(goods){
    this.index=-1;
    this.num=0;
    this.count=0;
    this.goods=goods;

    this.realGoods=[]; 


    goods.forEach(good => { 

        for (let i = 0; i < good.num; i++) { 
            let rg=deepClone(good);
            rg.index=i;
            this.realGoods.push(rg);
        }  
        good.count=good.num;
        this.num+=good.num;
        this.count=this.num;
    });
    // console.log(this.count);
    this.next=function(){
        if(this.realGoods.length<(this.index+1)){
           return null;
        }
        let oo=this.realGoods[++this.index]

        // console.log("next:"+this.index+":"+JSON.stringify(oo));
        return oo; 
    }
    // this.thisPutIn=function(i){
    //     // console.log("thisPutIn");
    //     this.realTimeGoods.splice(i,1);
    //     this.count=this.realTimeGoods.length;
    // }
}


//clone
export  function deepClone(source){
    const targetObj = source.constructor === Array ? [] : {}; // 判断复制的目标是数组还是对象
    for(let keys in source){ // 遍历目标
        if(Object.prototype.hasOwnProperty.call(source,keys)){
        if(source[keys] && typeof source[keys] === 'object'){ // 如果值是对象，就递归一下
            targetObj[keys] = source[keys].constructor === Array ? [] : {};
            targetObj[keys] = deepClone(source[keys]);
        }else{ // 如果不是，就直接赋值
            targetObj[keys] = source[keys];
        }
        }
    }
    return targetObj;
}

//计算列表里的体积 和
function calcBulk(otherbox){
    otherbox.otherbox=otherbox.filter(ob=>{return !ob.isFalse});
    let bulk=0;
    //遍历 将所有的 剩余空间 加起来
    otherbox.forEach(ob=>{
        bulk=bulk+ ob.length*ob.width*ob.height;
    })
    return bulk;
}


//开始算
export  function doit(goods,boxes){
    // console.log(goods);
    // console.log(boxes);
 
    //
    let goodsRestrictionsManager =new GoodsRestrictionsManager(goods);
 
    let bestPutBoxResult={ 
        allcount:0,
        otherbox:[

        ],
        otherboxBulk:null
    };

    //集装箱遍历
    boxes.forEach(box => {
        console.log("当前集装箱体积："+(box.length*box.width*box.height));

        console.log(goodsRestrictionsManager.resGoods);
        goodsRestrictionsManager.resGoods.forEach(goods => { 

            let putBoxResult={
                allcount:0,
                otherbox:[
    
                ]
            };
            putBoxResult.box=box;
            let goodsManager= new GoodsManager(goods); 

            // putBox(goodsManager,box,putBoxResult,0,box,null); 
            putBox(goodsManager,box,null,putBoxResult); 
            putBoxResult.goodsManager=goodsManager;
            putBoxResult.otherboxBulk=calcBulk(putBoxResult.otherbox);
            let isBest=false;
            if(bestPutBoxResult.otherboxBulk==null||bestPutBoxResult.otherboxBulk>putBoxResult.otherboxBulk){
                isBest=true;
                bestPutBoxResult=deepClone(putBoxResult);
            }
            console.log("当前方法 剩余空间体积："+putBoxResult.otherboxBulk+ (isBest?" ----":"" ));

        }); 
    }); 
  
 

    return bestPutBoxResult;
}

 

function putBox(goodsManager,box,prevGood,putBoxResult){
    //取下一个货物
    let good=goodsManager.next();
    if(good!=null){
        
        //坐标
        let vPos={};


        if(prevGood==null){ 
            //上一个货物为空 那这个是第一个货物
            vPos={
                x:0,
                y:0,
                z:0 
            };

            /*保存一下最大的尺寸 以防止不同尺寸商品导致 货物布局"穿模"*/
            putBoxResult.maxVal={
                length:good.length,
                width:good.width,
                height:good.height
            }
            /**/

            //第一个都放不下。。。
            if((vPos.x+good.width)>box.width || (vPos.y+good.height)>box.height || (vPos.z+good.length)>box.length){
                
                let otherbox={ 
                    pos:vPos,
                    height:box.height-vPos.y,
                    length:box.length-vPos.z,
                    width:box.width-vPos.x,
                    type:"beyond_"
                } 
                pushInOtherbox(putBoxResult.otherbox,otherbox)
                
                return ;
            }
        }else{
            
            


            //按标准 往右开始对方 虚拟坐标
            vPos={
                x:prevGood.pos.x+ (putBoxResult.maxVal.width>prevGood.width?putBoxResult.maxVal.width:prevGood.width),
                y:prevGood.pos.y,
                z:prevGood.pos.z
            };

            //如果 该坐标已经在 otherbox 中了 那。。。。
        }

        if((vPos.x+good.width)>box.width){
            //超宽  
            //宽度剩下空间先放入otherbox 
                 
            let otherbox={ 
                pos:deepClone(vPos),
                height:prevGood.height,
                length:prevGood.length,
                width:box.width-vPos.x ,
                type:"beyond_width"
            } 
            pushInOtherbox(putBoxResult.otherbox,otherbox) 


            
            
            //上移一层
            
            vPos.x=0;
            vPos.y=prevGood.pos.y+ (putBoxResult.maxVal.height>prevGood.height?putBoxResult.maxVal.height:prevGood.height);
            
            //上移一层厚 最大值没用了 清零让后面的判断覆盖该值
            putBoxResult.maxVal.height=0;
            putBoxResult.maxVal.width=0; 
            
            if((vPos.y+good.height)>box.height){ 
                //超高 
                //高度剩下空间先放入otherbox 
                 
                let otherbox={ 
                    pos:deepClone(vPos),
                    height:box.height-vPos.y,
                    length:prevGood.length,
                    width:box.width-vPos.x ,
                    type:"beyond_height"
                }             
                pushInOtherbox(putBoxResult.otherbox,otherbox)  

              
              
              
              
                //外移一层 
                vPos.y=0;
                vPos.z=prevGood.pos.z+(putBoxResult.maxVal.length>prevGood.length?putBoxResult.maxVal.length:prevGood.length);
                
                //上移一层厚 当前最大的深度没用了 清零让后面的判断覆盖该值
                putBoxResult.maxVal.length=0;

                if((vPos.z+good.length)>box.length){ 
                    let otherbox={ 
                        pos:vPos,
                        height:box.height-vPos.y,
                        length:box.length-vPos.z,
                        width:box.width-vPos.x ,
                        type:"beyond"
                    } 
                    pushInOtherbox(putBoxResult.otherbox,otherbox)   
                    return ;
              }
            } 
        }


        /*保存一下最大的尺寸 以防止不同尺寸商品导致 货物布局"穿模"*/
        if(good.length>putBoxResult.maxVal.length){
            putBoxResult.maxVal.length=good.length;
        }
        if(good.width>putBoxResult.maxVal.width){
            putBoxResult.maxVal.width=good.width;
        }
        if(good.height>putBoxResult.maxVal.height){
            putBoxResult.maxVal.height=good.height;
        }
        /**/



        good.pos=vPos;
        putBoxResult.allcount++;
        putBox(goodsManager,box,good,putBoxResult);


        
    }else{
        //一般为三部分
        let vPos1={
            x:prevGood.pos.x+prevGood.width,
            y:prevGood.pos.y,
            z:prevGood.pos.z
        };
        let otherbox1={ 
            pos:vPos1,
            height:box.height-vPos1.y,
            length:box.length-vPos1.z,
            width:box.width-vPos1.x ,
            type:"notnext_1"
        } 
        pushInOtherbox(putBoxResult.otherbox,otherbox1)   


        let vPos2={
            x:0,
            y:prevGood.pos.y+prevGood.height,
            z:prevGood.pos.z
        };
        let otherbox2={ 
            pos:vPos2,
            height:box.height-vPos2.y,
            length:box.length-vPos2.z,
            width:box.width-otherbox1.width-vPos2.x ,
            type:"notnext_2"
        } 
        pushInOtherbox(putBoxResult.otherbox,otherbox2);


        let vPos3={
            x:0,
            y:0,
            z:prevGood.pos.z+prevGood.length
        };
        let otherbox3={ 
            pos:vPos3,
            height:box.height-otherbox2.height,
            length:box.length-vPos3.z,
            width:box.width-otherbox1.width ,
            type:"notnext_3"
        } 
        pushInOtherbox(putBoxResult.otherbox,otherbox3); 



        if(prevGood.pos.y!=0){
            //最后不是最低
            //这种情况下还会余下一点 


            let vPos4={
                x:prevGood.pos.x+prevGood.width,
                y:0,
                z:prevGood.pos.z+prevGood.length
            };
            let otherbox4={ 
                pos:vPos4,
                height:box.height-otherbox1.height,
                length:box.length-vPos4.z,
                width:box.width-otherbox3.width,
                type:"notnext_4"
            } 
            pushInOtherbox(putBoxResult.otherbox,otherbox4);  

        }

    }
}


function CanMergeHandler(array,otherbox){ 
    for (let index = 0; index < array.length; index++) { 
        let ob = array[index];  
        if(ob.isFalse){
            continue;
        }
        if( ob.length==otherbox.length&&ob.height==otherbox.height&&ob.width==otherbox.width 
            && ob.pos.x==otherbox.pos.x&& ob.pos.y==otherbox.pos.y && ob.pos.z==otherbox.pos.z
            )
        {
            //自己 pass
            continue;
        }
        if(otherbox.type==ob.type){
            if(otherbox.pos.x==ob.pos.x && otherbox.pos.z==ob.pos.z && otherbox.width==ob.width && otherbox.length==ob.length){ 
                // if((otherbox.pos.y-otherbox.height)==ob.pos.y ){ 
                    ob.height+=otherbox.height;
                    otherbox.isFalse=true; 
                    CanMergeHandler(array,ob); 
                // } 
            } 
            if(otherbox.pos.x==ob.pos.x && otherbox.pos.y==ob.pos.y && otherbox.height==ob.height && otherbox.width==ob.width){ 
                // if((otherbox.pos.z-otherbox.length)==ob.pos.z){
                    ob.length+=otherbox.length;
                    otherbox.isFalse=true; 
                    CanMergeHandler(array,ob); 
                // }
            }   
        }   
    }

    
 
}



function pushInOtherbox(array,otherbox){ 
    if(otherbox.height==0||otherbox.width==0||otherbox.length==0) return ;

    for (let index = 0; index < array.length; index++) {
        let ob = array[index]; 
        if(ob.isFalse){
            continue;
        }
        if(ob.type==otherbox.type){ 
            
            if(otherbox.pos.x==ob.pos.x && otherbox.pos.z==ob.pos.z && otherbox.width==ob.width && otherbox.length==ob.length){ 
                // if((otherbox.pos.y-otherbox.height)==ob.pos.y ){ 
                    ob.height+=otherbox.height;
                    otherbox.isFalse=true; 
                    CanMergeHandler(array,ob); 
                // } 
            } 
            if(otherbox.pos.x==ob.pos.x && otherbox.pos.y==ob.pos.y && otherbox.height==ob.height && otherbox.width==ob.width){ 
                // if((otherbox.pos.z-otherbox.length)==ob.pos.z){
                    ob.length+=otherbox.length;
                    otherbox.isFalse=true; 
                    CanMergeHandler(array,ob); 
                // }
            } 
        }
    } 
    
    if(!otherbox.isFalse){
        array.push(otherbox);
    }
 

}





 
