<template> 
    <div ref="canvas" id="goodcanvas" ></div> 
</template>

<script>
import * as THREE from 'three'
import CameraControls from 'camera-controls'
CameraControls.install( { THREE: THREE } )
export default {
  name: 'goodcanvas',
  props: {
      good:{
            type: Object,
            default: function() {
                return {
                    color:'#FF0000',
                    length:100,
                    width:100,
                    height:100
                };
            }
      }
  }, 
  watch:{
            "good.color": function() {  
                this.addGeometry();
            },
            "good.length": function() {  
                this.addGeometry();
            },
            "good.width": function() {  
                this.addGeometry();
            },
            "good.height": function() {  
                this.addGeometry();
            },
            "good.restrictionflag": function() {    
                this.changeRotation();
            }
        },

  data:()=>({
    canvas:{
      width:null,
      height:null
    },
    scene:null, // 场景
    renderer:null,// 渲染器
    camera:null,// 透视相机
    cameraOpt:{
      fov:45,
      near:1,
      far:10000
    },
    light:null,   //光线 
    group:null,
    
    vv:""
  }),
  created(){
    this.$nextTick(()=>{
      this.threeStart();
    });
  }, 
  methods:{
    initThree() {
        
      this.canvas.width = this.$refs.canvas.clientWidth;
      this.canvas.height = this.$refs.canvas.clientHeight;
      this.renderer = new THREE.WebGLRenderer({
          antialias : true
      });
      this.renderer.setSize(this.canvas.width, this.canvas.height); 
      this.$refs.canvas.appendChild(this.renderer.domElement);
      this.renderer.setClearColor(0xFFFFFF, 1.0);

          
    },
    initScene() {
      this.scene = new THREE.Scene();
      
      
      
    },
    initCamera() { 
      this.camera = new THREE.PerspectiveCamera(this.cameraOpt.fov, this.canvas.width / this.canvas.height, this.cameraOpt.near, this.cameraOpt.far);
      //camera = new THREE.OrthographicCamera( window.innerWidth / - 2, window.innerWidth / 2, window.innerHeight / 2, window.innerHeight / - 2, 10, 1000 );
      
      
      
      this.camera.position.x = 100;
      this.camera.position.y = 60 ;
      this.camera.position.z = 90;
      
      this.camera.lookAt({
          x : 0,
          y : 50,
          z : 0
      }); 
      //按住鼠标转
      // new OrbitControls( this.camera, this.renderer.domElement );
      // const cameraControls = 
    
    },  
    initClockAndCameraControls(){
      this.clock = new THREE.Clock();
      this.cameraControls=new CameraControls(this.camera, this.renderer.domElement );
    },
    initLight() { 

      let ambientLight = new THREE.AmbientLight( 0xeeeeee,1);
      this.scene.add( ambientLight );

      // let lights = [];
      // lights[ 0 ] = new THREE.PointLight( 0xffffff, 1, 0 );
      // lights[ 1 ] = new THREE.PointLight( 0xffffff, 1, 0 );
      // lights[ 2 ] = new THREE.PointLight( 0xffffff, 1, 0 );

      // lights[ 0 ].position.set( 300, 500, 300 );
      // lights[ 1 ].position.set( 700, 500, 700 );
      // lights[ 2 ].position.set( - 500, - 500, - 300 );

      // this.scene.add( lights[ 0 ] );
      // this.scene.add( lights[ 1 ] );
      // this.scene.add( lights[ 2 ] );
    },
 
    animation()
    {  
        // let delta = this.clock.getDelta();
        // this.cameraControls.update( delta );
        this.renderer.render(this.scene, this.camera); 
        requestAnimationFrame(this.animation);
        
        // this.vv=" x:"+parseInt(this.camera.position.x) +"   y:"+ parseInt(this.camera.position.y) +"    z:"+ parseInt(this.camera.position.z) ;
        // this.vv+="         x:"+parseInt(this.camera.rotation.x) +"   y:"+ parseInt(this.camera.rotation.y) +"    z:"+ parseInt(this.camera.rotation.z) ;
    },
    threeStart() { 
      this.initThree();
      this.initCamera(); 
      this.initClockAndCameraControls(); 
      this.initScene();
      this.initLight(); 
    
      this.addGeometry();
      this.center();
      // this.horizontal();
      this.animation();

    },

    center(){
      var geometry = new THREE.BoxGeometry( 0.01, 0.01, 0.01 );
      var material = new THREE.MeshBasicMaterial( {color: 0x888888} );
      var cube = new THREE.Mesh( geometry, material );
      this.scene.add( cube );
    },
    horizontal(){
      var size = 6000;
      var divisions = 50;
      var gridHelper = new THREE.GridHelper(size, divisions, 0x1231c3, 0x135133);
      this.scene.add(gridHelper); 
    }
    ,
    addGeometry(){
        if(this.group!=null){
            this.scene.remove(this.group);
            this.group=null;
        }
        // color,length,width,height
        let color=this.good.color;
        let length=this.good.length;
        let width=this.good.width; 
        let height=this.good.height;

        let max=length>width?(length>height?length:height):(width>height?width:height);
        
        // color=color.replace("#","");
        length= length/max*50;
        width= width/max*50;
        height= height/max*50;
        

        this.group = new THREE.Group();
        let geometry = new THREE.CubeGeometry(width,height,length,0,0,0);
        let material =new THREE.MeshPhongMaterial( {
          color: color,
          side: THREE.DoubleSide,
          flatShading: THREE.FlatShading
        } ) 

        let cube = new THREE.Mesh(geometry, material); 
        // cube.position = new THREE.Vector3(0,0,0); 
        
         
        var line = new THREE.Line( geometry, new THREE.LineDashedMaterial( { color: 0x000000 } ) );

        this.group.add(cube,line);
        this.group.position.x=0;
        this.group.position.y=0;
        this.group.position.z=0; 
        this.scene.add( this.group );

    }, 
    changeRotation(){
 
        
        switch (this.good.restrictionflag) {
          case "x-": 
            this.group.rotation.x=0;
            this.group.rotation.y=0;
            this.group.rotation.z=0; 
      
            break;
          case "x|":
            
            this.group.rotation.x=0;
            this.group.rotation.y=Math.PI / 2;
            this.group.rotation.z=0;

            break;
          case "y-": 

            this.group.rotation.x=0;
            this.group.rotation.y=0;
            this.group.rotation.z=Math.PI / 2;

            break;
          case "y|": 
            this.group.rotation.x=0;
            this.group.rotation.y=Math.PI / 2;
            this.group.rotation.z=Math.PI / 2;

            break;
          case "z-": 
            this.group.rotation.x=Math.PI / 2;
            this.group.rotation.y=0;
            this.group.rotation.z=0;
            break;
          case "z|": 
            this.group.rotation.x=Math.PI / 2;
            this.group.rotation.y=0;
            this.group.rotation.z=Math.PI / 2;
            break; 
        }
    }
     
  }
}
</script>

